const hasOwnProperty = Object.prototype.hasOwnProperty;

/**
 * 
 * @param {*} obj 
 * @param {*} key key
 */
export function hasOwn(obj, key) {
  return hasOwnProperty.call(obj, key);
};

/**
 * private
 * ,export
 * @param {*} to 
 * @param {*} _from 
 */
function extend(to, _from) {
  for (let key in _from) {
    to[key] = _from[key];
  }
  return to;
};

/**
 * 
 * @param {*} arr 
 */
export function toObject(arr) {
  var res = {};
  for (let i = 0; i < arr.length; i++) {
    if (arr[i]) {
      extend(res, arr[i]);
    }
  }
  return res;
};

/**
 * 
 * @param {*} object 
 * @param {*} prop 
 */
export const getValueByPath = function(object, prop) {
  prop = prop || '';
  const paths = prop.split('.');
  let current = object;
  let result = null;
  for (let i = 0, j = paths.length; i < j; i++) {
    const path = paths[i];
    if (!current) break;

    if (i === j - 1) {
      result = current[path];
      break;
    }
    current = current[path];
  }
  return result;
};

/**
 * 
 * @param {*} obj object
 * data: '',
 * id: 'id',
 * pid: 'id',
 * label: '',
 * root: 'id/'
 */
export const array2tree = function(obj) {
  var data = obj.data;
  var idField = obj.id;
  var pidField = obj.pid;
  var labelField = obj.label;
  var root = {};
  if (typeof obj.root === 'object') {
    root = obj.root;
  } else {
    var tempObj = {};
    tempObj[idField] = obj.root;
    root = tempObj;
  }
  var children = [];
  var rId = '' + root[idField];
  for (var i in data) {
    var d = data[i];
    if (rId === '' + d[idField]) {
      root = d;
    } else if (rId === '' + d[pidField]) {
      children.push(d);
    }
  }
  root.id = root[idField];
  root.label = root[labelField];
  root.children = children;
  for (var j in root.children) {
    root.children[j] = array2tree({
      data: data,
      id: idField,
      pid: pidField,
      label: labelField,
      root: root.children[j]
    });
  }
  return root;
};



