<template>
  <el-select 
    v-model="selectedVal" 
    :multiple="multiple" 
    :disabled="disabled"
    :multiple-limit="multipleLimit"
    :size="size" 
    :name="name"
    :clearable="clearable" 
    :placeholder="placeholder"
    :filterable="filterable"
    :filter-method="filterMethod"
    @change="change" 
    @visible-change="visibleChange" 
    @remove-tag="removeTag" 
    @clear="clear">
    <el-option v-for="item in typeOptions" 
      :key="item.key" 
      :value="item.key" 
      :label="item.value">
      <slot name="option" :item="item"></slot>
    </el-option>
  </el-select>
</template>

<script>
  export default {
    name: 'ElSelectX',
    props: {
      /**  */
      props: {
        type: Object,
        default: function() {
          return { key: 'key', value: 'value' };
        }
      },
      /**  */
      dataParams: {
        type: Object,
        default: function() {
          return {};
        }
      },
      /** URL */
      dataUrl: String,
      /** code */
      dataCode: String,
      /**  */
      requestType: {
        type: String,
        default: 'GET'
      },
      jsonData: {
        type: String,
        default: 'data'
      },
      options: {
        type: Array,
        default: function() {
          return [];
        }
      },
      // el-selectelement-ui
      name: {
        type: String,
        default: 'el-select-x'
      },
      size: String,
      disabled: {
        type: Boolean,
        default: false
      },
      clearable: {
        type: Boolean,
        default: true
      },
      multiple: Boolean,
      multipleLimit: {
        type: Number,
        default: 0
      },
      placeholder: {
        type: String,
        default: ''
      },
      filterable: {
        type: Boolean,
        default: true
      },
      filterMethod: Function,
      value: {
        required: true
      }
    },
    data: function() {
      var selValue = this.multiple ? [] : '';
      selValue = this.value ? this.value : selValue;
      return {
        selectedVal: selValue,
        typeOptions: this.options
      };
    },
    watch: {
      selectedVal: function(val) {
        this.$emit('input', val);
      },
      value: function(val) {
        if (this.selectedVal !== val) {
          this.selectedVal = val;
        }
      },
      dataUrl: function(url) {
        if (url) {
          this.query();
        }
      },
      options: function(data) {
        if (data) {
          this.typeOptions = data;
        }
      },
      dataCode: function(code) {
        if (code) {
          var _this = this;
          yufp.lookup.bind(code, function(options) {
            _this.typeOptions = options;
          });
        }
      }
    },
    created: function() {
      if (!this.dataUrl && !this.dataCode) {
        var listData = [];
        for (var i = 0, len = this.typeOptions.length; i < len; i++) {
          var obj = this.typeOptions[i];
          listData.push({
            key: obj[this.props.key],
            value: obj[this.props.value]
          });
        }
        this.typeOptions = listData;
      } else if (!this.dataUrl && this.dataCode) {
        var thiz = this;
        yufp.lookup.bind(this.dataCode, function(options) {
          thiz.typeOptions = options;
        });
      } else {
        this.query();
      }
    },
    methods: {
      change: function(val) {
        this.$emit('change', val);
      },
      visibleChange: function(val) {
        this.$emit('visible-change', val);
      },
      removeTag: function(tag) {
        this.$emit('remove-tag', tag);
      },
      getSelectdText: function() {
        var text = '';
        for (var i = 0; i < this.typeOptions.length; i++) {
          var obj = this.typeOptions[i];
          if (obj[this.props.key] === this.selectedVal) {
            text = obj[this.props.value];
            break;
          }
        }
        return text;
      },
      getSelectdValue: function(value) {
        return this.selectedVal;
      },
      setSelectdByValue: function(value) {
        var isok = false;
        for (var i = 0; i < this.typeOptions.length; i++) {
          var obj = this.typeOptions[i];
          if (obj[this.props.value] === value) {
            this.selectedVal = obj[this.props.key];
            isok = true;
            break;
          }
        }
        if (!isok) {
          alert('');
        }
      },
      setSelectdByItem: function(item) {
        if (item && !isNaN(item)) {
          if (this.typeOptions.length < item) {
            alert('item');
          } else {
            this.selectedVal = this.typeOptions[item][this.props.key];
          }
        } else {
          alert('');
        }
      },
      clear: function() {
        this.selectedVal = '';
      },
      query: function() {
        var _this = this;
        _this.typeOptions = [];
        yufp.service.request({
          method: _this.requestType,
          name: _this.dataUrl,
          data: _this.dataParams,
          callback: function(code, message, response) {
            var data1 = _this.getObjectKey(response, _this.jsonData);
            data1 = data1 && data1.length > 0 ? data1 : [];
            for (var i = 0, len = data1.length; i < len; i++) {
              var obj = data1[i];
              if (obj[_this.props.key]) {
                _this.typeOptions.push({
                  key: obj[_this.props.key],
                  value: obj[_this.props.value]
                });
              }
            }
          }
        });
      },
      getObjectKey: function(obj, ns) {
        if (!ns) {
          return obj;
        }
        var keys = ns.split('.');
        for (var i = 0, len = keys.length; i < len; i++) {
          if (!obj) {
            break;
          }
          obj = obj[keys[i]];
        }
        return obj;
      }
    }
  };
</script>




