<template>
  <el-radio-group 
    v-model='radio' 
    :disabled='disabled' 
    :name='name' 
    @change='change'>
    <el-radio 
      v-if='!optionButton' 
      v-for='item in radioData' 
      :label='item.key' 
      :key='item.key' 
      :disabled='disabled'>
      {{item.value}}
    </el-radio>
    <el-radio-button 
      v-if="optionButton" 
      v-for='item in radioData' 
      :label='item.key' 
      :key='item.key' 
      :disabled='disabled'>
      {{item.value}}
    </el-radio-button>
  </el-radio-group>
</template>

<script>
  export default {
    name: 'ElRadioX',
    props: {
      disabled: Boolean,
      name: String,
      dataUrl: String,
      dataCode: String,
      /**  */
      dataParams: {
        type: Object,
        default: function() {
          return {};
        }
      },
      /**  */
      requestType: {
        type: String,
        default: 'GET'
      },
      jsonData: {
        type: String,
        default: 'data'
      },
      options: {
        type: Array,
        default: function() {
          return [];
        }
      },
      value: String,
      // buttonfalse
      optionButton: {
        type: Boolean,
        default: function() {
          return false;
        }
      }
    },
    data: function() {
      return {
        radio: this.value ? this.value : '',
        radioData: ''
      };
    },
    mounted: function() {
      if (this.options) {
        var listData = [];
        for (var i = 0, len = this.options.length; i < len; i++) {
          var obj = this.options[i];
          listData.push({ value: obj.value, key: obj.key });
        }
        this.radioData = listData;
      }
      if (this.dataUrl) {
        this.query();
      }
      if (this.dataCode) {
        this.setradioData(this.dataCode);
      }
    },
    methods: {
      query: function() {
        var me = this;
        yufp.service.request({
          method: me.requestType,
          url: this.dataUrl,
          data: this.dataParams,
          callback: function(code, message, response) {
            var data = me.getObjectKey(response, me.jsonData) || [];
            data = data && data.length > 0 ? data : [];
            for (var i = 0; i < data.length; i++) {
              var obj = data[i];
              me.radioData.push({ value: obj.value, key: obj.key });
            }
          }
        });
      },
      setradioData: function(dataCode) {
        var _this = this;
        yufp.lookup.bind(dataCode, function(options) {
          _this.radioData = options;
        });
      },
      change: function(val) {
        this.$emit('change', val);
      },
      getObjectKey: function(obj, ns) {
        if (!ns) {
          return obj;
        }
        var keys = ns.split('.');
        for (var i = 0, len = keys.length; i < len; i++) {
          if (!obj) {
            break;
          }
          obj = obj[keys[i]];
        }
        return obj;
      }
    },
    watch: {
      dataCode: function(val) {
        this.setradioData(val);
      },
      radio: function(val) {
        this.$emit('input', val);
      },
      value: function(val) {
        if (this.radio !== val) {
          this.radio = val;
        }
      }
    }
  };
</script>




