<template>
  <el-checkbox-group 
    v-model="checklist" 
    :min="min" 
    :max="max" 
    :name="name" 
    @change="change">
    <el-checkbox 
      v-if="!optionButton" 
      v-for="item in checkdata" 
      :label="item.key" 
      :key="item.key" 
      :disabled="disabled">
      {{item.value}}
    </el-checkbox>
    <el-checkbox-button 
      v-if="optionButton" 
      v-for="item in checkdata" 
      :label="item.key" 
      :key="item.key" 
      :disabled="disabled">
      {{item.value}}
    </el-checkbox-button>
  </el-checkbox-group>
</template>

<script>
  export default {
    name: 'ElCheckboxX',
    props: {
      min: Number,
      max: Number,
      disabled: Boolean,
      name: String,
      dataUrl: String,
      dataCode: String,
      /**  */
      dataParams: {
        type: Object,
        default: function() {
          return {};
        }
      },
      /**  */
      requestType: {
        type: String,
        default: 'GET'
      },
      jsonData: {
        type: String,
        default: 'data'
      },
      options: {
        type: Array,
        default: function() {
          return [];
        }
      },
      value: Array,
      // buttonflase
      optionButton: Boolean
    },
    data: function() {
      return {
        checklist: this.value ? this.value : [],
        checkdata: this.options
      };
    },
    created: function() {
      if (this.options) {
        var listData = [];
        for (var i = 0, len = this.checkdata.length; i < len; i++) {
          var obj = this.checkdata[i];
          listData.push({ value: obj.value, key: obj.key });
        }
        this.checkdata = listData;
      }
      if (this.dataUrl) {
        this.query();
      }
      if (this.dataCode) {
        this.setcheckData(this.dataCode);
      }
    },
    methods: {
      change: function(val) {
        this.$emit('change', val);
      },
      setcheckData: function(dataCode) {
        var _this = this;
        yufp.lookup.bind(dataCode, function(options) {
          _this.checkdata = options;
        });
      },
      query: function() {
        var me = this;
        yufp.service.request({
          method: this.requestType,
          url: this.dataUrl,
          data: this.dataParams,
          callback: function(code, message, response) {
            var data = me.getObjectKey(response, me.jsonData) || [];
            data = data && data.length > 0 ? data : [];
            for (var i = 0; i < data.length; i++) {
              var obj = data[i];
              me.checkdata.push({ value: obj.value, key: obj.key });
            }
          }
        });
      },
      getObjectKey: function(obj, ns) {
        if (!ns) {
          return obj;
        }
        var keys = ns.split('.');
        for (var i = 0, len = keys.length; i < len; i++) {
          if (!obj) {
            break;
          }
          obj = obj[keys[i]];
        }
        return obj;
      }
    },
    watch: {
      dataCode: function(val) {
        this.setcheckData(val);
      },
      checklist: function(val) {
        this.$emit('input', val);
      },
      value: function(val) {
        if (this.checklist !== val) {
          this.checklist = val;
        }
      }
    }
  };
</script>




