<template>
  <transition name="dialog-fade">
    <div class="el-dialog-x__wrapper" v-show="visible" @click.self="handleWrapperClick"
      :style="styleRoot">
      <div
        class="el-dialog-x"
        :class="[sizeClass, customClass]"
        ref="dialog"
        :style="style">
        <div class="el-dialog-x__header">
          <slot name="title">
            <span class="el-dialog-x__title">{{title}}</span>
          </slot>
          <button type="button" class="el-dialog-x__headerbtn" aria-label="Close" 
            v-if="showClose" @click="handleClose">
            <i class="el-dialog-x__close el-icon el-icon-close"></i>
          </button>
        </div>
        <div class="el-dialog-x__body" v-if="rendered" :style="styleBody"><slot></slot></div>
        <div class="el-dialog-x__footer" v-if="$slots.footer || needBar">
          <el-button v-if="needBar" type="primary" icon="yx-undo2" @click="handleClose">{{cancelText}}</el-button>
          <el-button v-if="needBar" type="primary" icon="check" @click="sureFn">{{sureText}}</el-button>
          <slot name="footer"></slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import Popup from 'element-ui/src/utils/popup';
  import emitter from 'element-ui/src/mixins/emitter';

  export default {
    name: 'ElDialogX',

    mixins: [Popup, emitter],

    props: {
      title: {
        type: String,
        default: ''
      },

      modal: {
        type: Boolean,
        default: true
      },
  
      modalAppendToBody: {
        type: Boolean,
        default: true
      },

      appendToBody: {
        type: Boolean,
        default: true
      },

      lockScroll: {
        type: Boolean,
        default: true
      },

      closeOnClickModal: {
        type: Boolean,
        default: false
      },

      closeOnPressEscape: {
        type: Boolean,
        default: false
      },

      showClose: {
        type: Boolean,
        default: true
      },

      size: {
        type: String,
        default: 'x'
      },

      customClass: {
        type: String,
        default: ''
      },

      top: {
        type: String,
        default: '15%'
      },

      beforeClose: Function,

      width: String,
      height: String,
      needBar: Boolean,
      sureFn: {
        type: Function,
        default() {}
      },
      cancelText: {
        type: String,
        default: ''
      },
      sureText: {
        type: String,
        default: ''
      }
    },

    watch: {
      visible(val) {
        this.$emit('update:visible', val);
        if (val) {
          this.$emit('open');
          this.$el.addEventListener('scroll', this.updatePopper);
          this.$nextTick(() => {
            this.$refs.dialog.scrollTop = 0;
          });
          if (this.appendToBody) {
            document.body.appendChild(this.$el);
          }
        } else {
          this.$el.removeEventListener('scroll', this.updatePopper);
          this.$emit('close');
          if (this.appendToBody && this.$el && this.$el.parentNode) {
            this.$el.parentNode.removeChild(this.$el);
          }
        }
      }
    },

    computed: {
      sizeClass() {
        return 'el-dialog-x--' +
          (this.size === 'full' ? 'full' : 'x');
      },
      style() {
        return this.size === 'full' ? {} : { 'top': this.top };
      },
      styleRoot() {
        var orginWidth = this.width;
        var newWidth = '50%';
        var marginLeft = '-25%';
        if (orginWidth && orginWidth !== 'auto') {
          newWidth = orginWidth;
          marginLeft = '-' + (parseInt(orginWidth, 10) / 2);
          var pix = /^\d+(px)?$/i;
          // var percent = /^\d+%$/i;
          marginLeft += pix.test(orginWidth) ? 'px' : '%';
        }
        return this.size === 'full'
          ? {}
          : { width: newWidth, left: '50%', marginLeft: marginLeft };
      },
      styleBody() {
        return (this.size === 'full' || !this.height)
          ? {}
          : {height: this.height, overflow: 'hidden', overflowY: 'auto'};
      }
    },

    methods: {
      handleWrapperClick() {
        if (!this.closeOnClickModal) return;
        this.handleClose();
      },
      handleClose() {
        if (typeof this.beforeClose === 'function') {
          this.beforeClose(this.hide);
        } else {
          this.hide();
        }
      },
      hide(cancel) {
        if (cancel !== false) {
          this.$emit('update:visible', false);
          this.$emit('visible-change', false);
        }
      },
      updatePopper() {
        this.broadcast('ElSelectDropdown', 'updatePopper');
        this.broadcast('ElDropdownMenu', 'updatePopper');
      }
    },

    mounted() {
      if (this.visible) {
        this.rendered = true;
        this.open();
        if (this.appendToBody) {
          document.body.appendChild(this.$el);
        }
      }
    },

    destroyed() {
      // if appendToBody is true, remove DOM node after destroy
      if (this.appendToBody && this.$el && this.$el.parentNode) {
        this.$el.parentNode.removeChild(this.$el);
      }
    }
  };
</script>



